.modal {
	position: fixed;
	top: 0;
	bottom: 0;
	right: 0;
	left: 0;
	display: none;
	background-color: rgba(0, 0, 0, 0.5);
}

.modal__content {
	padding: 25px;
	background-color: #fffff1;
	border-radius: 25px;
	min-width: 250px;
}

.modal.active {
	display: flex;
	justify-content: center;
	align-items: center;
}
